import {
  reqShopBooks
} from '../api'

import {
  ADD_TO_BOOKS
} from './mutation-types'

export default {
  async reqShopBooks ({commit}) {
    const result = await reqShopBooks()
    if (result.code === 0) {
      const books = result.data
      commit(ADD_TO_BOOKS, {books})
    }
  }
}



